.promo {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  box-sizing: border-box;
  padding-top: 180px;
  background: url("../../img/intro-desktop@1x.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 463px;

  @media (min-resolution: 2dppx) {
    background: url("../../img/intro-desktop@2x.jpg");
    background-size: cover;
  }
}

.promo__title {
  @include font-set(45px, 58.5px, 700);
  font-family: $secondary-font;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 0.03em;
  color: $white;
  margin: 0;
}

.promo__slogan {
  @include font-set(38px, 49.4px, 400);
  font-family: $secondary-font;
  font-style: italic;
  text-align: center;
  align-self: center;
  color: $white;
  margin: 0;
  width: 80vw;
}
